window.dom = {
    create(string){
        const container = document.createElement("template");
        //è¿™é‡Œç”¨templateæ ‡ç­¾ä»£æ›¿div,è¿™æ ·æ‰èƒ½å®¹çº³ä»»æ„æ ‡ç­¾
        container.innerHTML = string.trim();
        //trim()å¯ä»¥åŽ»é™¤ç©ºæ ¼
        return container.content.firstChild;
        //templateéœ€è¦åŠ ä¸ªcontentæ‰èƒ½è¯»å–
    },//ç”¨äºŽåˆ›é€ ä»»æ„ä¸€ä¸ªå…ƒç´ 

    after(node,node2){
        node.parentNode.insertBefore(node2,node.nextSibling)
        //å°†node2æ’åˆ°nodeçš„åŽé¢,ç”±äºŽapiçš„ç¼ºå¤±åªèƒ½ç»•ä¸€åœˆ,éžå¸¸åäººç±»
    },

    before(node,node2){
        node.parentNode.insertBefore(node2,node);
    },

    append(parent,node){
        parent.appendChild(node);
    },//æ–°å¢žå„¿å­,append:å¢žåŠ 

    warp(node,parent){
        dom.before(node,parent);
        dom.append(parent,node);
    },//æ–°å¢žçˆ¸çˆ¸

    remove(node){
        // node.remove();æ–°api IEå¯èƒ½ä¸æ”¯æŒ
        node.parentNode.removeChild(node);
        return node;
    },
    
    empty(node){
        const {childNodes} = node
        //const childNodes = node.chilNodesçš„ç®€å†™
        const array = []
        let x = node.firstChild;
        while(x){
            array.push(dom.remove(node.firstChild));
            //removeä¼šè¿”å›žè‡ªå·±
            x = node.firstChild;
        }
        return array;
    },//åˆ é™¤åŽä»£

    attr(node, name, value){ // jsåªèƒ½è¿™ä¹ˆé‡è½½
        if(arguments.length === 3){
            node.setAttribute(name,value);
        }else if(arguments.length === 2){
            return node.getAttribute(name);
        }
    },//ä¿®æ”¹nodeçš„æŸé¡¹å±žæ€§

    text(node, string){
        if(arguments.length === 2){
            if('innerText' in node){//é€‚é…
                node.innerHTML = string; 
            }else{
                node.textContent = string 
            }
        }else if(arguments.length === 1){
            if('innerText' in node){//é€‚é…
                return node.innerHTML; 
            }else{
                return node.textContent; 
            }
        }
    },

    html(node,string){
        if(arguments.length === 2){
            node.innerHTML = string;
        }else if(arguments.length === 1){
            return node.innerHTML;
        }
    },

    style(node,name,value){
        if(arguments.length === 3){
            //dom.style(div,'color','red')
            node.style[name] = value;
        }else if(arguments.length === 2){
            //dom.style(div,'color')
            return node.style[name];
        }else if(name instanceof Objcet){//æ£€æµ‹nameå±žæ€§æ˜¯ä¸æ˜¯åœ¨Objcetçš„åŽŸåž‹é“¾é‡Œ
            for(let key in name){
                node.style[key] = object[key];
                // for inè¯­å¥å¾ªçŽ¯éåŽ†æ•°ç»„
            }
        }
    },

    class: {
        add(node,className){
            node.classList.add(className);
        },
        remove(node,className){
            node.classList.remove(className);
        },
        has(node,className){
            return node.classList.contains(className);
        },
    },

    on(node, eventName, fn){
        node.addEventListner(eventName,fn)
    },
    off(node, eventName, fn){
        node.removeEventListner(eventName,fn)
    },//æ·»åŠ äº‹ä»¶ç›‘å¬
    find(selector,scope){
        return (scope || document).querySelectorAll(selector);
    },//|| çŸ­è·¯é€»è¾‘:å–é‡åˆ°çš„ç¬¬ä¸€ä¸ªçœŸå€¼æˆ–è€…æœ€åŽé¢çš„å€¼
    parent(node){
        return node.parentNode;
    },
    children(node){
        return node.children;
    },
    siblings(node){
        return Array.from(node.parentNode.children).filter(n=>n!==node);
        //filterè¿‡æ»¤è‡ªå·±
    },
    next(node){
        let x = node.nextSibling;
        while(x && x.nodeType === 3){//1æ˜¯èŠ‚ç‚¹,3æ˜¯æ–‡æœ¬
            x = x.nextSibling;
        }  
        return x;
    },
    previous(node){
        let x = node.previousSibling;
        while(x && x.nodeType === 3){//1æ˜¯èŠ‚ç‚¹,3æ˜¯æ–‡æœ¬
            x = x.previousSibling;
        }  
        return x;
    },
    each(nodeList,fn){
        for(let i=0;i<nodeList.length;i++){
            fn.call(null,nodeList[i]);
        }
    },
    index(node){
        const list = dom.children(node.parentNode)
        let i;
        for(i=0;i<list.length;i++){
            if(list[i] === node){
                break;
            }
        }
        return i;
    }
};

